
import { defineComponent } from 'vue';
import { RaidRole } from '@/enums';

export default defineComponent({
  props: {
    role: {
      type: String,
      required: true,
      validator(val: any) {
        return Object.values(RaidRole).includes(val);
      },
    },
    counter: {
      type: Number,
      required: true,
    },
    numberOfPugs: {
      type: Number,
      default: 0,
    },
  },
});
